@media screen and (min-width: 576px) {
  .slider-summary-container-new {
    position: absolute;
    top: 0;
    right: 8px; } }

.slider-summary-container-new .prefix-suffix {
  font-size: 18px;
  font-weight: bold;
  color: #46737A; }

.slider-summary-container-new .value {
  font-size: 18px;
  font-weight: 800;
  color: #354F53; }

.slider-container-v3 {
  margin-bottom: 10px;
  position: relative;
  margin-left: auto;
  margin-right: auto; }
  .slider-container-v3 .line {
    width: 100%;
    height: 1px;
    border: solid 1px #C8E0E4; }
  .slider-container-v3 .line-green {
    position: absolute;
    top: 0;
    height: 1px;
    border: solid 1px #00D7AF; }
  .slider-container-v3 .color-knob {
    background-color: #00D7AF; }
  .slider-container-v3 .white-knob {
    background-color: #ffffff; }
  .slider-container-v3 .slider-knob {
    position: absolute;
    left: 0;
    width: 21px;
    height: 21px;
    transform: translateX(-0.5px) translateY(-0.5px);
    box-shadow: 0 2px 8px 0 rgba(70, 115, 122, 0.2);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 3; }
    .slider-container-v3 .slider-knob:focus, .slider-container-v3 .slider-knob:active {
      outline: none; }
    .slider-container-v3 .slider-knob:hover {
      animation: zoom-in-out 1s infinite alternate;
      cursor: pointer; }

@keyframes zoom-in-out {
  from {
    transform: translateX(-0.5px) translateY(-0.5px) scale(1); }
  to {
    transform: translateX(-0.5px) translateY(-0.5px) scale(1.15); } }
    .slider-container-v3 .slider-knob .inner {
      width: 7px;
      height: 7px;
      border-radius: 50%; }
  .slider-container-v3 .knob-container {
    position: absolute;
    left: 0;
    top: -13px;
    height: 30px;
    width: calc(100% - 15px);
    display: flex;
    align-items: center; }
    .slider-container-v3 .knob-container .knob {
      position: absolute;
      left: 50px;
      width: 20px;
      height: 20px;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      z-index: 2; }
      .slider-container-v3 .knob-container .knob .desc {
        position: absolute;
        transform: translateY(-40px);
        min-width: 100px;
        font-size: 12px;
        font-weight: v;
        color: #6E7F95;
        text-align: center;
        transition: color 0.3s ease;
        bottom: 0; }
      .slider-container-v3 .knob-container .knob .desc-bottom {
        transform: translateY(-20px); }
      .slider-container-v3 .knob-container .knob .desc-active {
        color: #46737A; }
    .slider-container-v3 .knob-container .knob-default {
      background-color: #C8E0E4; }
      .slider-container-v3 .knob-container .knob-default:after {
        content: '';
        position: absolute;
        top: 30%;
        left: 30%;
        bottom: 30%;
        right: 30%;
        border-radius: 50%;
        background-color: white; }
    .slider-container-v3 .knob-container .knob-done {
      width: 20px;
      height: 20px;
      background-color: #00D7AF;
      background-image: url(../../../assets/images/common/Tick.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 60%; }
    .slider-container-v3 .knob-container .knob:first-child {
      justify-content: flex-start; }
      .slider-container-v3 .knob-container .knob:first-child .desc {
        text-align: left;
        margin-left: -5px; }
    .slider-container-v3 .knob-container .knob:last-child {
      justify-content: flex-end; }
      .slider-container-v3 .knob-container .knob:last-child .desc {
        text-align: right; }
