.loading-screen-v2 {
  background: linear-gradient(150.14deg, #6480EB 0.6%, #433BC4 100%);
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  transition: z-index 0.3s step-end, opacity 0.3s linear; }
  .loading-screen-v2.loading-screen-active {
    opacity: 0.9;
    z-index: 1051;
    transition: z-index 0.3s step-start, opacity 0.3s linear; }
  .loading-screen-v2 .spinner-animation {
    top: calc(45% - 40px);
    width: 60px !important;
    height: 60px !important; }
    .loading-screen-v2 .spinner-animation .dot1,
    .loading-screen-v2 .spinner-animation .dot2 {
      width: 30px !important;
      height: 30px !important;
      background-color: #F2FAF9 !important; }
  .loading-screen-v2 .dot1, .loading-screen-v2 .dot2 {
    background-color: #F2FAF9 !important; }
  .loading-screen-v2 .bottom-container {
    position: absolute;
    top: 50%;
    width: 230px;
    left: calc(50% - 115px);
    overflow-x: hidden;
    overflow-y: hidden; }
    .loading-screen-v2 .bottom-container p {
      font-weight: bold;
      font-size: 12px;
      color: white;
      text-align: center;
      margin: 0; }
  .loading-screen-v2 .logo-container {
    display: flex; }
    .loading-screen-v2 .logo-container img {
      height: 64px;
      width: 64px;
      margin: 0 10px; }

@keyframes animate-active {
  0% {
    opacity: 0;
    z-index: -1; }
  1% {
    z-index: 9999; }
  100% {
    opacity: 0.9; } }

@keyframes animate-active {
  0% {
    opacity: 0.9;
    z-index: 9999; }
  1% {
    opacity: 0; }
  100% {
    z-index: -1; } }

.loading-modal {
  max-width: 480px; }
  .loading-modal .modal-body {
    padding-top: 0;
    padding-bottom: 0; }
  .loading-modal .modal-footer button {
    margin: auto; }
